
.centered {
  position: fixed; /* or absolute */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.svg-map {
  width: auto;
  height: 100vh;
  stroke: #a1a1a1;
  stroke-width: 1;}
  .svg-map__location {
    fill: #866daf;
    cursor: pointer; }
  .svg-map__location:focus, .svg-map__location:hover {
    fill: #a791ca;
    outline: 0; }
  .svg-map__location[aria-checked=true] {
    fill: #29c0a1 }

.svg-map__location {
    cursor: pointer;
    shape-rendering: crispEdges;
    fill: transparent;
}